<template>
  <div class="success">
    <p>验证完成,注册成功,{{num}}s钟后自动跳转</p>
  </div>
</template>

<script>
import axios from 'axios'
// import tipInput from '@/components/TipInput/tipInput'
export default {
  data () {
    return {
      num:5
    }
  },
  created () {
    
  },
  mounted(){
      this.jump()
  },
  methods:{
      jump(){
          var that = this
          var num = that.num
          var aa = setInterval(function(){
                num--
                that.num = num
                if(num == 0){
                    clearInterval(aa)
                }
          },1000)
          setTimeout(function(){
              that.$router.push("/login")
          },5000)
      }
    }
}
</script>

<style lang="scss" scoped>
.success{
    font-size: 30px;
    padding-top: 30px;
} 
</style>
<style>
.headerWrapper .el-dropdown{
  color: #000;
  font-size: 16px;
}
</style>



